import React, { useEffect } from 'react'
import styled from 'styled-components'
import html from '../Resource/Image/Language/html.jpg'
import css from '../Resource/Image/Language/CSS.jpg'
import js from '../Resource/Image/Language/JS.jpg'
import git from '../Resource/Image/Language/git.jpg'
import mongodb from '../Resource/Image/Language/mongodb.jpg'
import nodejs from '../Resource/Image/Language/nodejs.jpg'
import react from '../Resource/Image/Language/react.jpg'
import reactredux from '../Resource/Image/Language/native.jpg'
import mern from '../Resource/Image/Language/exp.jpg'
import Aos from "aos";
import "aos/dist/aos.css";
import TextButton from './TextButton'

const Skills = () => {
    useEffect(() => {
        Aos.init({duration:2000})
       })
    const skills = [
        {
            img:html,
            title:'HTML'
        },
        {
            img:css,
            title:'CSS'
        },
        {
            img:js,
            title:'Javascript'
        },
        {
            img:git,
            title:'Git/Github'
        },
        {
            img:mongodb,
            title:'MongoDB'
        },
        {
            img:nodejs,
            title:'NodeJS'
        },
        {
            img:react,
            title:'ReactJS'
        },
        {
            img:reactredux,
            title:'Redux'
        },
        {
            img:mern,
            title:'MERN'
        }
    ]

  

  return (
    <Container id='skills' data-aos='flip-down'>
        <TextButton text={"Skills"}/>
        <div className='skillsContainer'>
            {skills.map((ele,index)=>(
                <div key={index}>
                    <img src={ele.img} alt={ele.title} width='70px' height='70px'/>
                    <p>{ele.title}</p>
                </div>
            ))}
        </div>
    </Container>
  )
}


const Container = styled.div`
    width:100%;
    margin:auto;
    marginTop:50px
    
    h1{
        color:teal;
        text-transform:uppercase;
       
    }

    .skillsContainer{
        width:85%;
        margin:auto;
        display:grid;
        grid-template-columns: repeat(auto-fit,minmax(120px,max-content));
        justify-content:center;
        padding:30px;
        gap:40px;
    }

    .skillsContainer>div{
       display:flex;
       align-items:center;
       flex-direction:column;
       box-shadow: rgba(0, 0, 0, 0.15) 0px 5px 15px 0px;
       padding: 20px 0px 10px 0px;
       border-radius: 10px;
    }

    .skillsContainer>div:hover{
        -webkit-transform: translateY(-5px);
        transform: translateY(-15px);
        -webkit-transition: all 0.5s;
        transition: all 0.8s;
     }
   
`

export default Skills